$green: #4AF6CD;

//Global
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: 'Fira Sans', sans-serif;
  font-weight: 400;
  margin: 0;
  font-size: 16px;
  background-color: #171919;
  color: #FFFFFF;
  text-transform: uppercase;
  min-width: 320px;
  overflow-x: hidden;
}

a {
  display: inline-block;
  text-decoration: none;
  color: #FFFFFF;
}

.link {
  cursor: pointer;

  &:hover {
    transform: scale(1.1);
    transition: .4s;
  }
}

.linkTo{
  cursor: pointer;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1920px;
  margin: 0 auto;
}

.headLine-left {
  font-size: 22px;
  line-height: 32px;
  letter-spacing: 7.33333px;
  opacity: 0.5;
  padding-left: 103px;
  position: relative;
  display: inline-block;

  &::before {
    content: '';
    position: absolute;
    width: 80px;
    height: 1px;
    background-color: #FFFFFF;
    left: 0;
    top: 50%;
  }
}

.headline-right {
  font-size: 22px;
  line-height: 32px;
  letter-spacing: 7.33333px;
  opacity: 0.5;
  padding-right: 103px;
  position: relative;
  display: inline-block;

  &::before {
    content: '';
    position: absolute;
    width: 80px;
    height: 1px;
    background-color: #FFFFFF;
    right: 0;
    top: 50%;
  }
}

.title {
  div {
    display: inline-block;
    font-weight: 800;
    font-size: 200px;
    color: rgba(#FFFFFF, .02);
    position: relative;
    transform: translateX(-50px);
  }

  span {
    display: inline-block;
  }

  h3 {
    position: absolute;
    top: 0;
    right: -5px;
    margin: 0;
    color: #FFFFFF;
    font-size: 80px;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 80px;
      height: 1px;
      background-color: $green;

    }
  }
}

.subtitle {
  display: inline-block;
  text-align: right;

  &__head {
    font-weight: 800;
    font-size: 40px;
    padding-right: 10px;

    span {
      font-weight: 100;
    }
  }
}

.descr {
  display: inline-block;
  padding-left: 5px;

  &__head {
    font-weight: 800;
    font-size: 40px;

    span {
      font-weight: 100;
    }
  }
}

.btn {
  font-weight: 800;
  font-size: 40px;
  line-height: 36px;

  span {
    padding: 17px 57px 15px 17px;
    background-color: $green;
    display: inline-block;
    margin-left: 19px;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      width: 20px;
      height: 1px;
      background-color: #FFFFFF;
      top: 50%;
      left: -10px;
    }

    &::after {
      content: '';
      position: absolute;
      width: 25px;
      height: 21px;
      background-image: url("./images/arrows-right.svg");
      top: 50%;
      right: 17px;
      transform: translateY(-50%);
    }
  }
}

.serf, .holder {
  padding: 160px 0 120px;
}

.holder {
  padding-bottom: 265px;
  margin-bottom: 35px;
}

.star-list {
  li {
    display: inline-block;

    & + li {
      margin-left: -25px;
    }
  }
}
.slider-arrow{
  &:hover {
    transform: scale(1.1);
    transition: .4s;
    opacity: 1;
  }
}



